/* App.css */

.App {
  text-align: center;
  margin-top: 50px;
}

.typing-area {
  margin-bottom: 20px;
}

input[type="text"] {
  padding: 15px;
  font-size: 24px;
  margin-right: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
  width: 360px; 
  text-align: center; 
}

.timer {
  margin-top: 10px;
  font-size: 24px;
}

.speed {
  margin-top: 10px;
  font-size: 24px;
}

button {
  margin-top: 10px;
  margin-right: 10px;
  padding: 15px 30px;
  font-size: 20px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  transition: all 0.3s ease;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

button:hover {
  background-color: #0056b3;
}
